import React from 'react'

const Contact = () => {
  return (
    <div className='container-fluid my-5' id="contact" style={{backgroundColor:'#0c091f'}}>
        <div className='container'>
            <div className='row mt-5'>
                <h2 className='text-white' style={{marginTop:70}}>Contact Us</h2>
                <p className='text-white my-5' style={{fontSize:18}}>Do you have any questions? Please do not hesitate to contact. I will come back to you within a matter of hours to help you.</p>
            </div>
            <div className='row'>
                <div className='col-sm-12 col-lg-10 mb-4'>
                <div className="row">
                    <div className="col-6">
                        <label for="name" className='text-white my-2' style={{fontSize:16}}>Your Name *</label>
                        <input type="text" class="form-control" aria-label="name" required/>
                    </div>
                    <div className="col-6">
                        <label for="Email" className='text-white my-2' style={{fontSize:16}}>Your Email *</label>
                        <input type="text" class="form-control" aria-label="Email" required/>
                    </div>
                </div>
                   <div className='col-12 my-2'>
                   <label for="subject" className='text-white my-2' style={{fontSize:16}}>Your Subject</label>
                    <input type="text" class="form-control" aria-label="subject"/>
                   </div>
                   <div className='col-12 my-2' style={{display:'flex',flexDirection:'column'}}>
                   <label for="message" className='text-white my-2' style={{fontSize:16}}>Your message *</label>
                   <textarea name="message" rows="3" cols="100" aria-label='message' className='form-control' required></textarea>
                   </div>
                   <a href='mailto:pritamjad@gmail.com'><input type='button' className='btn btn-primary my-3' value="Send"/></a>
                </div>
                <div className='col-2 d-none d-lg-block' style={{display:'flex',flexDirection:'column',alignItems:'center'}}>
                <div className='mt-2' style={{textAlign:'center'}}>
                <i class="fa-sharp fa-solid fa-location-dot" style={{fontSize:30,color:'green'}}></i>
                <p className='text-white'>Jamshedpur, Jharhand, India</p>
                </div>
                <div className='mt-5' style={{textAlign:'center'}}>
                <i className="fa-solid fa-phone" style={{fontSize:30,color:'green'}}></i>
                <p className='text-white'>+91 7903371032</p>
                </div>
                <div className='mt-5' style={{textAlign:'center'}}>
                <i className="fa-solid fa-envelope" style={{fontSize:30,color:'green'}}></i>
                <p className='text-white'>pritamjad@gmail.com</p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Contact;